.Button {
  display: block;
  all: unset;
  color: var(--color-pink);
  border: 1px solid var(--color-pink);
  border-radius: 5px;
  padding: 4px 16px;
  transition: .5s;
  cursor: pointer;
}

.Button:hover {
  color: var(--color-input-color);
  border-color: var(--color-input-color);
}